"use client";

import { useEffect, useState } from "react";
import { Job } from "@/types/jobs";
import Image from "next/image";
import Filters from "@/components/jobs/filters";
import { Filter } from "@/types/filter";
import { Company } from "@/types/company";
import jobsData from "@/app/data/jobs.json"; // Import statically for client-side
import { JSX } from "react";

export default function Jobs() {
  const [selectedFilters, setSelectedFilters] = useState({
    company: [] as string[],
    location: [] as string[],
    employmentType: [] as string[],
  });

  const [filters, setFilters] = useState<Filter>({
    company: [],
    location: [],
    employmentType: [],
    map: function (arg0: (item: Company | string) => JSX.Element): import("react").ReactNode {
      throw new Error("Function not implemented.");
    }
  });

  const [jobs, setJobs] = useState<Job[]>([]);

  useEffect(() => {
    const filters: Filter = {
      location: [],
      company: [],
      employmentType: [],
      map: function (arg0: (item: Company | string) => JSX.Element): import("react").ReactNode {
        throw new Error("Function not implemented.");
      }
    };

    const uniqueLocations = new Set<string>();
    const uniqueCompanies = new Map<string, Company>();
    const uniqueTypes = new Set<string>();

    jobsData.forEach((job: Job) => {
      uniqueLocations.add(job.location);
      uniqueCompanies.set(job.company.id, job.company);
      uniqueTypes.add(job.description.employmentType);
    });

    filters.location = Array.from(uniqueLocations);
    filters.company = Array.from(uniqueCompanies.values());
    filters.employmentType = Array.from(uniqueTypes);

    setFilters(filters);
    setJobs(jobsData);
  }, []);

  const handleFilterChange = (category: string, values: string[]) => {
    setSelectedFilters((prev) => ({
      ...prev,
      [category]: values,
    }));
  };

  const filteredJobs = jobs.filter((job) => {
    const matchCompany =
      selectedFilters.company.length === 0 ||
      selectedFilters.company.includes(job.company.id);

    const matchLocation =
      selectedFilters.location.length === 0 ||
      selectedFilters.location.includes(job.location);

    const matchType =
      selectedFilters.employmentType.length === 0 ||
      selectedFilters.employmentType.includes(job.description.employmentType);

    return matchCompany && matchLocation && matchType;
  });

  return (
    <div className="w-full flex flex-col items-center justify-center">
      <Filters filter={filters} onFilterChange={handleFilterChange} />
      <div className="w-full flex flex-row flex-wrap items-center justify-center">
        {filteredJobs.map((job: Job) => (
          <div
            key={job.id}
            className="p-5 rounded-xl shadow-md border-1 border-slate-200 min-w-1/3 max-w-3/4 m-2"
          >
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-xl font-bold">{job.title}</h2>
                <p>{job.company.name}</p>
              </div>
              <div>
                <Image
                  src={job.company.logo ?? "/placeholder-logo.png"}
                  alt={job.company.name}
                  width={50}
                  height={50}
                  className="rounded-full"
                />
              </div>
            </div>
            <p>{job.location}</p>
            <p>{job.description.role}</p>
            <p>{job.description.industry}</p>
            <p>{job.description.employmentType}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
