import { Filter, Options } from "@/types/filter";
import MultiFilterDropDown from "./dropdown";
import { Company } from "@/types/company";

export default function Filters({ filter }: { filter: Filter }) {
  const filterCategories: Options[] = [
    {
      name: "Company Name",
      key: "company",
      isObject: true,
      option: {
        label: "name",
        value: "id",
      },
    },
    {
      name: "Location Details",
      key: "location",
    },
    {
      name: "Employment Type",
      key: "employmentType",
    },
  ];

  return (
    <div className="flex flex-wrap mx-auto justify-around align-middle my-10 p-3 bg-white border-1 border-slate-200 w-3/4">
      {filterCategories.map((category) => {
        // Ensuring type safety with proper dynamic key access
        const rawData = filter[category.key as keyof Filter];

        // Type checking the rawData based on the category
        const data =
          Array.isArray(rawData) &&
          rawData.every(
            (item) =>
              typeof item === "string" ||
              (typeof item === "object" && item !== null && "id" in item)
          )
            ? (rawData as string[] | Company[])
            : [];

        return (
          <div key={category.key} className="flex flex-col mb-2">
            <h2 className="text-lg font-semibold font-sans text-slate-600 mb-2">
              {category.name}
            </h2>
            <div className="flex flex-col">
              <MultiFilterDropDown
                options={category}
                filterData={data}
                categoryName={category.name}
              />
            </div>
          </div>
        );
      })}
    </div>
  );
}
